import { Component, OnInit } from '@angular/core';
import * as AOS from 'aos';
import { Title, Meta } from '@angular/platform-browser';
import { TranslateService } from '@ngx-translate/core';
import { LanguageService } from "src/app/services/language/language.service"
import { Location } from '@angular/common';
import { Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  title = 'ezzalibrahim-portfolio';

  constructor(
    private titleService: Title,
    private metaService: Meta,
    private translateService: TranslateService,
    private location: Location,
    private languageService: LanguageService
  ) {
  }
  ngOnInit(): void {

    this.languageService.initLanguage()


    this.titleService.setTitle("Brahim Ezzali | Full Stack Developer");

    this.metaService.addTags([
      { name: 'keywords', content: 'Full Stack , Backend , Frontend, software, developer' },
      { name: 'description', content: 'I am an experienced Full Stack Developer with a high level of expertise in web development. Throughout my career, I have successfully completed multiple projects, both independently and as part of professional teams.' },
    ]);


    AOS.init();

  }
}
